<template>
    <div class="outerBox">
		<div class="innerBox">
			<img class="cultureIcon" :src="iconSrc" alt="">
			<div class="cultureInfo">
				<div class="cultureName">
					{{itemName}}
					<div class="tag" :style="{backgroundColor: tagColor}">
						<span style="margin: 0 0.3em">{{tag}}</span>
					</div>
				</div>
				<div class="valueRow">
					<span class="envValue">{{value}}</span>
					<span class="unit">{{unit}}</span>
				</div>
			</div>
		</div>
		<div class="subTextBox">
			<span class="subText">{{subText}}</span>
		</div>
	</div>
</template>

<style lang="less" scoped>
.outerBox {
	height: 100%;
	border: 1px solid;
	border-image: linear-gradient(270deg, rgba(255, 208, 59, 1), rgba(255, 208, 59, 0.2)) 1 1;
	background: linear-gradient(90deg, rgba(72,64,42,0.4) 0%, rgba(61,61,61,0) 100%);

	.innerBox {
		width: 77.22%;
		height: 49.48%;
		margin: auto;
		margin-top: 1.11vh;
		display: flex;
		align-items: center;

		.cultureIcon {
			height: 100%;
			aspect-ratio: 1/1;
			border-radius: 4px;
		}
		.cultureInfo {
			margin-left: 6.66%;
			height: 70%;
			width: 100%;

			.cultureName {
				height: 45.45%;
				font-size: 72.72%;
				line-height: 100%;
				display: flex;
				align-items: center;
				color: #fff;
				font-family: 'pingfangM', sans-serif;
				font-weight: 500;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;

				.tag {
					height: 80%;
					display: flex;
					align-items: center;
					justify-content: center;
					font-size: 71.43%;
					color: #fff;
					font-family: 'titleFont', sans-serif;
					line-height: 100%;
					border-radius: 0.6vh;
					margin-left: 2.3%;
				}
			}
			
			.valueRow {
				height: 61.36%;
				width: 100%;
				
				.envValue {
					line-height: 100%;
					font-size: 100%;
					background: linear-gradient(180deg, #fff 42%, #ffd03b 100%);
					-webkit-background-clip: text;
					-webkit-text-fill-color: transparent;
					font-family: 'titleFont', sans-serif;
					font-weight: 400;
				}
				.unit {
					font-size: 50%;
					color: #fff;
					font-family: 'pingfangM', sans-serif;
					font-weight: 500;
					margin-left: 3%;
				}
			}
		}
	}
	.subTextBox {
		width: 100%;
		height: 40.21%;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 1.11vh;
		font-family: 'timerFont', sans-serif;
		color: #fff
	}
}
</style>

<script>
export default {
	props:{
		inputData:{
			type: Object
		}
	},
	data(){
		return {
			itemName: '',
			iconSrc: '',
			value: 0,
			unit: '',
			subText: '',
			tag: '',
			tagColor: ''
		}
	},
	mounted(){
		let {itemName, iconSrc, value, unit, subText, tag, tagColor} = this.inputData;
		let ctx = require.context('@/assets/page3',false,/.png$/);
		this.iconSrc = ctx(iconSrc);
		this.itemName = itemName;
		this.value = value;
		this.unit = unit;
		this.subText = subText;
		this.tag = tag;
		this.tagColor = tagColor;
		return false;
	}
}
</script>

